.container3{display:flex;justify-content:center;align-items:center;text-align:center;padding:2rem;background-image:url("../../assets/picture/BG/背景-防空洞.png");background-size:cover;background-position:center;height:100vh;width:100vw}.container3 .content3{display:flex;flex-direction:column;align-items:center;max-width:90%;margin:0 auto;color:#fff;font-family:Arial,Helvetica,sans-serif;font-weight:bold}.container3 .content3 .text3{margin-bottom:1rem;font-size:2.5rem}.container3 .content3 .button3{padding:1.5em 3em;font-size:2rem;background-color:#4caf50;color:#fff;border:none;border-radius:5px;cursor:pointer;margin-top:2.5rem;transition:background-color .3s ease}.container3 .content3 .button3:hover{background-color:#45a049}.container3 .content3 .endButton3{margin-top:3rem}.container3 .content3 .finalButton3{margin-top:3rem}.container3 .content3 .input3{padding:1em;font-size:1.5rem;width:100%;max-width:400px;margin-bottom:1.25rem;border:1px solid #ccc;border-radius:5px}@media screen and (max-width: 768px){.container3 .content3 .text3{font-size:1.5rem}.container3 .content3 .input3{font-size:2rem}.container3 .content3 .button3{font-size:1rem}}