*{margin:0;padding:0;box-sizing:border-box;font-family:"Chocolate Classical Sans",sans-serif;font-weight:400;font-style:normal}html,body{width:100vw;height:100vh;margin:0;padding:0;overflow:hidden}.special-message{width:100vw;height:100vh;margin:0;padding:20px;background-color:green;color:#fff;z-index:999;display:flex;justify-content:center;align-items:center;text-align:center;font-size:1.2rem}body{background-image:url(./assets/picture/BG/背景-活動中心.png);background-size:cover;background-repeat:no-repeat;background-position:center;display:flex;justify-content:center;align-items:center;flex-direction:column;overflow:hidden}.container,.centered-container{display:flex;justify-content:center;align-items:center;flex-direction:column;text-align:center;width:100%;height:100%}.dialog{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;height:100%;position:relative}.centered-image{width:100%;height:auto;object-fit:cover}.foundButton{width:20vw;height:15vh;display:flex;justify-content:center;align-items:center;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);margin:0;padding:0;background:none;border:none;border-radius:10px;cursor:pointer;transition:transform .2s}.foundButton img{width:100%;height:100%;border-radius:10px}.foundButton:active{transform:translate(-50%, -50%) scale(0.95)}.intro-container{display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:rgba(255,255,255,.9);padding:20px;border-radius:10px;text-align:center;box-shadow:0 4px 8px rgba(0,0,0,.2);font-size:1.2rem}.intro-container p{margin-bottom:20px;line-height:1.5}.intro-container button{padding:10px 20px;background-color:#4caf50;color:#fff;border:none;border-radius:5px;cursor:pointer;transition:background-color .3s}.intro-container button:hover{background-color:#45a049}@media screen and (max-width: 768px){.foundButton{width:40vw;height:20vh}.centered-image{width:100%}}@media screen and (orientation: portrait){html,body{transform:none;width:100%;height:100%}}